@import "../../global.scss";

.navbar{
    width:100%;
    height:70px;
    background-color: $mainColor4;
    color: white;
    position: fixed;
    top: 0;
    z-index: 6;
    transition: all 0.6s ease-in;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        justify-content: space-between;

        .left{
            display:flex;
            align-items: center;
            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile {
                    display: none;
                }

                .icon{
                    font-size: 28px;
                    margin-right: 5px;
                }

                span{
                    font-size: 15px;
                    font-weight: 600;
                }
            }
        }
        .right{
            margin-top: 13px;
            .hamburger{
                overflow: hidden;
                width: 32px;
                height: 25px;
                display:flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span{
                    width: 100%;
                    height: 3px;
                    background-color: white;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active{
        background-color: white;
        color:$mainColor4;

        .hamburger{
            span{
                &:first-child{
                    background-color: $mainColor4;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: $mainColor4;
                    transform: rotate(-45deg);

                }
            }
        }
    }
}