@import "../../global.scss";

.testimonials{
    background-color: white;
    display:flex;
    flex-direction: column;
    align-items: center;

    @include mobile{
        justify-content: space-around;
    }

    h1{
        font-size: 50px;
        margin: 20px 0;
        color: $mainColor7;
        font-weight: 700;

        @include mobile{
            height: 10%;
        }
    }

    .container{
        width:100%;
        height: 80%;
        display: flex;
        justify-content: center;
        align-items: center;

        @include mobile{
            flex-direction: column;
            height: 100%;
        }

        .card{
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0 0 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;

            @include mobile{
                height: 180px;
                margin: 10px 0;
            }

            &.featured{
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile{
                    width: 250px;
                    height: 180px;
                    margin: 1px;
                }
            }

            .top{
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    &.left, &.right {
                        height: 25px;

                        @include mobile{
                            height: 20px;
                            width: 20px;
                            overflow: hidden;
                        }
                    }

                    &.user{
                        height: 60px;
                        width: 60px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 30px;

                        @include mobile{
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }

            .center{
                padding: 10px;
                border-radius: 10px;
                background-color: $mainColor4;
                color: white;
                text-align: center;

                @include mobile{
                    font-size: 12px;
                    padding: 5px;
                }
            }

            .bottom{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                h3{
                    margin-bottom: 5px;
                    color: $mainColor4;

                    @include mobile{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}